div.knob.component label, div.knob.component input {
  display: none; }

div.knob.component .knob.body {
  height: 80px;
  width: 80px;
  position: relative;
  cursor: pointer;
  transform-origin: center; }
  div.knob.component .knob.body .hexagon.knob {
    fill: #000;
    stroke-linejoin: round;
    stroke: #000;
    stroke-width: 0.618; }
  div.knob.component .knob.body .hexagon.notch {
    stroke: linen;
    stroke-width: 0.1;
    fill: mintcream; }

div.knob.component .knob.body:focus {
  outline: none; }
