main {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center; }
  main #device {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    width: 250px;
    background: lightslategray;
    border-radius: 5px;
    border: solid #000 1px;
    padding: 30px 10px 10px; }
  main .break {
    flex-basis: 100%; }
  main .h-space {
    flex-grow: 1; }
  main .v-space {
    height: 20px; }
  main label, main .label {
    color: #fff; }
  main #volume-label,
  main #freq-label {
    flex-grow: 1; }
  main #volume-label {
    text-align: right;
    padding-right: 20px; }
  main #freq-label {
    text-align: left;
    padding-left: 20px; }
